import Vue from 'vue';
Vue.use(require('vue-moment'));
import moment from 'moment';
export default {
    data: () => ({
      moment: moment
   }),
  // Fetching Single BlogPost
  async asyncData({ params, payload }) {
    if (payload) return { blogPost: payload };
    else
      return {
        blogPost: await require(`~/assets/content/blog/${params.blog}.json`)
      };
  }
};
